import { useState, useEffect } from 'react';
import { z } from 'zod';
import { MapPin, Calendar, DollarSign, Users, Hotel, Utensils, Plane, Clock } from 'lucide-react';
import { motion } from 'framer-motion';
import type { TripFormData } from '../types/trip';

const tripSchema = z.object({
  destination: z.string().min(2, 'Destination must be at least 2 characters'),
  days: z.number().min(1, 'Trip must be at least 1 day').max(30, 'Trip cannot exceed 30 days'),
  budget: z.number().min(1, 'Budget must be greater than 0'),
  currency: z.string().min(3, 'Currency is required'),
  travel_style: z.enum(['Solo', 'Family', 'Backpacker']),
  starting_location: z.string().min(2, 'Starting location must be at least 2 characters'),
  month: z.string().min(3, 'Travel month is required'),
});

interface TripFormProps {
  onSubmit: (data: TripFormData) => void;
  isLoading: boolean;
  initialData?: Partial<TripFormData>;
}

export default function TripForm({ onSubmit, isLoading, initialData }: TripFormProps) {
  const [formData, setFormData] = useState<TripFormData>({
    destination: '',
    days: 3,
    budget: 1000,
    currency: 'USD',
    travel_style: 'Solo',
    starting_location: '',
    dietary_preference: 'no-preference',
    month: 'January',
    ...initialData
  });

  // Update form if initialData changes (e.g. from URL params)
  useEffect(() => {
    if (initialData) {
      setFormData(prev => ({ ...prev, ...initialData }));
    }
  }, [initialData]);

  const [errors, setErrors] = useState<Record<string, string>>({});

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    try {
      const validatedData = tripSchema.parse(formData);
      setErrors({});
      onSubmit(validatedData);
    } catch (error) {
      if (error instanceof z.ZodError) {
        const newErrors: Record<string, string> = {};
        // In Zod v4, validation errors are in the 'issues' property
        if (error.issues && Array.isArray(error.issues)) {
          error.issues.forEach((issue) => {
            if (issue.path && issue.path.length > 0) {
              newErrors[issue.path[0] as string] = issue.message;
            }
          });
        }
        setErrors(newErrors);
      } else {
        // Handle non-Zod errors
        console.error('Form validation error:', error);
      }
    }
  };

  const handleChange = (field: keyof TripFormData, value: string | number) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-8 bg-white/80 dark:bg-slate-900/80 backdrop-blur-xl p-8 rounded-[2rem] shadow-2xl max-w-2xl w-full border border-white/40 dark:border-white/10 relative overflow-hidden transition-colors duration-500">
      {/* Gloss Effect */}
      <div className="absolute top-0 left-0 w-full h-2 bg-gradient-to-r from-transparent via-white/50 dark:via-white/10 to-transparent" />

      <div className="text-center mb-8 relative z-10">
        <div className="inline-block p-3 rounded-2xl bg-blue-50/50 dark:bg-blue-900/30 mb-4 backdrop-blur-sm border border-blue-100/50 dark:border-blue-800/30">
          <Plane className="w-8 h-8 text-blue-600 dark:text-blue-400" />
        </div>
        <h1 className="text-4xl font-extrabold text-transparent bg-clip-text bg-gradient-to-r from-blue-700 to-purple-700 dark:from-blue-400 dark:to-purple-400 mb-3 tracking-tight">
          Plan Your Adventure
        </h1>
        <p className="text-gray-600 dark:text-gray-300 font-medium text-lg">
          Let AI craft your perfect itinerary
        </p>
      </div>

      <div className="grid md:grid-cols-2 gap-6 relative z-10">
        {/* Destination */}
        <div className="md:col-span-2 group">
          <label className="flex items-center gap-2 text-sm font-bold text-gray-700 dark:text-gray-300 mb-2 ml-1">
            <MapPin className="w-4 h-4 text-blue-600 dark:text-blue-400" />
            Where to?
          </label>
          <div className="relative">
            <input
              type="text"
              value={formData.destination}
              onChange={(e) => handleChange('destination', e.target.value)}
              placeholder="e.g., Paris, Tokyo, Bali..."
              className={`w-full pl-12 pr-4 py-4 bg-white/50 dark:bg-slate-800/50 border rounded-2xl focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 focus:bg-white dark:focus:bg-slate-800 transition-all duration-300 backdrop-blur-sm outline-none text-lg dark:text-white dark:placeholder-gray-500 ${errors.destination ? 'border-red-500' : 'border-gray-200 dark:border-gray-700 hover:border-blue-300 dark:hover:border-blue-500'}`}
            />
            <MapPin className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 dark:text-gray-500 group-hover:text-blue-500 transition-colors" />
          </div>
          {errors.destination && <p className="text-red-500 text-sm mt-2 ml-1 font-medium flex items-center gap-1">‚ö†Ô∏è {errors.destination}</p>}
        </div>

        {/* Month & Days Grid */}
        <div className="space-y-2">
          <label className="flex items-center gap-2 text-sm font-bold text-gray-700 dark:text-gray-300 mb-2 ml-1">
            <Calendar className="w-4 h-4 text-blue-600 dark:text-blue-400" />
            When?
          </label>
          <div className="relative group">
            <select
              value={formData.month}
              onChange={(e) => handleChange('month', e.target.value)}
              className="w-full pl-12 pr-10 py-4 bg-white/50 dark:bg-slate-800/50 border border-gray-200 dark:border-gray-700 rounded-2xl focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 focus:bg-white dark:focus:bg-slate-800 transition-all backdrop-blur-sm outline-none appearance-none hover:border-blue-300 dark:hover:border-blue-500 cursor-pointer dark:text-white"
            >
              {['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'].map(m => (
                <option key={m} value={m} className="dark:bg-slate-800">{m}</option>
              ))}
            </select>
            <Calendar className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 dark:text-gray-500 group-hover:text-blue-500 transition-colors" />
            <div className="absolute right-4 top-1/2 -translate-y-1/2 pointer-events-none">
              <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7" /></svg>
            </div>
          </div>
        </div>

        <div className="space-y-2">
          <label className="flex items-center gap-2 text-sm font-bold text-gray-700 dark:text-gray-300 mb-2 ml-1">
            <Clock className="w-4 h-4 text-blue-600 dark:text-blue-400" />
            How long?
          </label>
          <div className="relative group">
            <input
              type="number"
              value={formData.days}
              onChange={(e) => handleChange('days', parseInt(e.target.value) || 1)}
              min="1"
              max="30"
              className={`w-full pl-12 pr-4 py-4 bg-white/50 dark:bg-slate-800/50 border rounded-2xl focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 focus:bg-white dark:focus:bg-slate-800 transition-all backdrop-blur-sm outline-none appearance-none dark:text-white ${errors.days ? 'border-red-500' : 'border-gray-200 dark:border-gray-700 hover:border-blue-300 dark:hover:border-blue-500'}`}
            />
            <span className="absolute right-4 top-1/2 -translate-y-1/2 text-gray-400 dark:text-gray-500 text-sm font-medium">Days</span>
            <Clock className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 dark:text-gray-500 group-hover:text-blue-500 transition-colors" />
          </div>
          {errors.days && <p className="text-red-500 text-sm mt-1 ml-1 font-medium">{errors.days}</p>}
        </div>

        {/* Budget */}
        <div className="md:col-span-2 space-y-2">
          <label className="flex items-center gap-2 text-sm font-bold text-gray-700 dark:text-gray-300 mb-2 ml-1">
            <DollarSign className="w-4 h-4 text-blue-600 dark:text-blue-400" />
            Budget (per person)
          </label>
          <div className="flex gap-3">
            <div className="relative w-32">
              <select
                value={formData.currency}
                onChange={(e) => handleChange('currency', e.target.value)}
                className="w-full pl-4 pr-8 py-4 bg-white/50 dark:bg-slate-800/50 border border-gray-200 dark:border-gray-700 rounded-2xl focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 focus:bg-white dark:focus:bg-slate-800 transition-all backdrop-blur-sm outline-none appearance-none cursor-pointer hover:border-blue-300 dark:hover:border-blue-500 font-medium text-center dark:text-white"
              >
                <option value="USD" className="dark:bg-slate-800">USD</option>
                <option value="INR" className="dark:bg-slate-800">INR</option>
                <option value="EUR" className="dark:bg-slate-800">EUR</option>
                <option value="GBP" className="dark:bg-slate-800">GBP</option>
              </select>
            </div>
            <div className="relative flex-1 group">
              <input
                type="number"
                value={formData.budget}
                onChange={(e) => handleChange('budget', parseFloat(e.target.value) || 0)}
                min="1"
                className={`w-full pl-12 pr-4 py-4 bg-white/50 dark:bg-slate-800/50 border rounded-2xl focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 focus:bg-white dark:focus:bg-slate-800 transition-all backdrop-blur-sm outline-none text-lg font-medium dark:text-white ${errors.budget ? 'border-red-500' : 'border-gray-200 dark:border-gray-700 hover:border-blue-300 dark:hover:border-blue-500'}`}
              />
              <DollarSign className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 dark:text-gray-500 group-hover:text-green-500 transition-colors" />
            </div>
          </div>
          {errors.budget && <p className="text-red-500 text-sm mt-1 ml-1 font-medium">{errors.budget}</p>}
        </div>

        {/* Travel Style - Visual Selection */}
        <div className="md:col-span-2 space-y-3">
          <label className="flex items-center gap-2 text-sm font-bold text-gray-700 dark:text-gray-300 ml-1">
            <Users className="w-4 h-4 text-blue-600 dark:text-blue-400" />
            Who's traveling?
          </label>
          <div className="grid grid-cols-3 gap-3">
            {[
              { id: 'Solo', icon: 'üë§', label: 'Solo' },
              { id: 'Family', icon: 'üë®‚Äçüë©‚Äçüëß‚Äçüë¶', label: 'Family' },
              { id: 'Backpacker', icon: 'üéí', label: 'Backpacker' }
            ].map((style) => (
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                key={style.id}
                type="button"
                onClick={() => handleChange('travel_style', style.id)}
                className={`flex flex-col items-center justify-center p-4 rounded-2xl border-2 transition-colors duration-200 ${formData.travel_style === style.id
                  ? 'border-blue-500 bg-blue-50/80 dark:bg-blue-900/60 text-blue-700 dark:text-blue-200 shadow-md'
                  : 'border-gray-100 dark:border-gray-700 bg-white/40 dark:bg-slate-800/40 text-gray-600 dark:text-gray-300 hover:border-blue-200 dark:hover:border-blue-700 hover:bg-white/60 dark:hover:bg-slate-800/60'
                  }`}
              >
                <span className="text-2xl mb-2">{style.icon}</span>
                <span className="font-bold text-sm">{style.label}</span>
              </motion.button>
            ))}
          </div>
        </div>

        {/* Dietary Preference - Chips */}
        <div className="md:col-span-2 space-y-3">
          <label className="flex items-center gap-2 text-sm font-bold text-gray-700 dark:text-gray-300 ml-1">
            <Utensils className="w-4 h-4 text-blue-600 dark:text-blue-400" />
            Food Preferences
          </label>
          <div className="flex flex-wrap gap-3">
            {[
              { id: 'no-preference', label: 'Anything üçΩÔ∏è' },
              { id: 'vegetarian', label: 'Vegetarian ü•¨' },
              { id: 'non-vegetarian', label: 'Non-Veg üçñ' }
            ].map((diet) => (
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                key={diet.id}
                type="button"
                onClick={() => handleChange('dietary_preference', diet.id)}
                className={`px-6 py-2 rounded-full text-sm font-bold border transition-colors duration-200 ${formData.dietary_preference === diet.id
                  ? 'bg-gradient-to-r from-orange-400 to-red-500 text-white border-transparent shadow-lg'
                  : 'bg-white/40 dark:bg-slate-800/40 text-gray-600 dark:text-gray-300 border-gray-200 dark:border-gray-700 hover:border-orange-300 hover:bg-white/60 dark:hover:bg-slate-800/60'
                  }`}
              >
                {diet.label}
              </motion.button>
            ))}
          </div>
        </div>

        {/* Starting Location */}
        <div className="md:col-span-2 group">
          <label className="flex items-center gap-2 text-sm font-bold text-gray-700 dark:text-gray-300 mb-2 ml-1">
            <Hotel className="w-4 h-4 text-blue-600 dark:text-blue-400" />
            Starting Point (Hotel/Location)
          </label>
          <div className="relative">
            <input
              type="text"
              value={formData.starting_location}
              onChange={(e) => handleChange('starting_location', e.target.value)}
              placeholder="e.g., Marriott Downtown or City Center"
              className={`w-full pl-12 pr-4 py-4 bg-white/50 dark:bg-slate-800/50 border rounded-2xl focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 focus:bg-white dark:focus:bg-slate-800 transition-all backdrop-blur-sm outline-none dark:text-white dark:placeholder-gray-500 ${errors.starting_location ? 'border-red-500' : 'border-gray-200 dark:border-gray-700 hover:border-blue-300 dark:hover:border-blue-500'}`}
            />
            <Hotel className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400 dark:text-gray-500 group-hover:text-blue-500 transition-colors" />
          </div>
          {errors.starting_location && <p className="text-red-500 text-sm mt-1 ml-1 font-medium">{errors.starting_location}</p>}
        </div>
      </div>

      <button
        type="submit"
        disabled={isLoading}
        className="w-full bg-gradient-to-r from-blue-600 via-indigo-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white font-bold py-5 px-6 rounded-2xl transition duration-300 disabled:opacity-50 disabled:cursor-not-allowed shadow-xl hover:shadow-2xl hover:-translate-y-1 transform active:scale-95 text-lg flex items-center justify-center gap-2 group relative overflow-hidden"
      >
        <span className="relative z-10 flex items-center gap-2">
          {isLoading ? (
            <>Generating Your Dream Trip...</>
          ) : (
            <>
              Generate Itinerary <Plane className="w-5 h-5 group-hover:translate-x-1 group-hover:-translate-y-1 transition-transform" />
            </>
          )}
        </span>
        <div className="absolute inset-0 bg-white/20 translate-y-full group-hover:translate-y-0 transition-transform duration-300 transform skew-y-12" />
      </button>
    </form>
  );
}
